.card {
    width: 100%;
    height: auto;
    margin: 0 auto;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    border: 3px solid gray;
}

#pic {
  width: 100%;
  height: 144px; 
  object-fit: fill;
}


.navbar {
    background-color: purple;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: center;
    /* display: inline; */
}

.navbar li {
    display: inline-block;
    float: none;
}

li {
    color: white;
    padding: 15px 0;
}


body {
    font-size: 24px;
}

#jumboPic {
    background-image: url("https://stmed.net/sites/default/files/styles/1920x1080/public/bob%27s-burgers-wallpapers-31529-6628927.jpg?itok=YDPw9bCG");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    color: white;
    text-shadow: 2px 2px 4px #000000;
}
.card-columns {
    -webkit-columns: 4;
            columns: 4;
    margin: 0% 10% 0% 10%;
}

#burger {
    background-image: linear-gradient(to bottom right, orange, yellow);
}
